.screen a {
  display: contents;
  text-decoration: none;
}

.full-width-a {
  width: 100%;
}

.full-height-a {
  height: 100%;
}

.container-center-horizontal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  pointer-events: none;
  width: 100%;
}

.container-center-horizontal > * {
  flex-shrink: 0;
  pointer-events: auto;
}

* {
  box-sizing: border-box;
}

body {
  background-color: black;
}
.login-container {
  
  margin-top: 3%;
  margin-bottom: 3%;
  display: grid;
  grid-template-columns: 1.2fr 1.2fr 0.6fr 1fr;
  grid-template-rows: 0.6fr 1.4fr 1fr;
  grid-auto-columns: 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-auto-flow: row;
}

.empty { grid-area: 1 / 1 / 2 / 2; }

.photo { grid-area: 1 / 2 / 4 / 5; }

.some-text {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    ". . .";
  grid-area: 1 / 2 / 2 ;
}

.login-form { 
  grid-area: 2 / 1 / 4 / 2; 
}

.custom-text {
  color: var(--white);
  flex: 1 1;
  font-family: var(--font-family-impact-regular);
  font-size: 80px;
  font-weight: 400;
  letter-spacing: 0;
  position: relative;
  top: -30px;
  left: -30px;
  line-height: 80px;
  margin-bottom: -2px;
  margin-right: -2px;
  width: 557px;
}

.formCenter {
  margin-bottom: 100px;
  width: 20%;
  float:left;
}

.formTitle {
  color: #707c8b;
  font-weight: 300;
  margin-bottom: 50px;
}

.formTitleLink {
  color: #707c8b;
  text-decoration: none;
  display: inline-block;
  font-size: 1.7em;
  margin: 0 10px;
  padding-bottom: 5px;
}

.formTitleLink:first-child {
  margin-left: 0;
}

.formField {
  margin-bottom: 40px;
  align-items: center;
}

.formFieldInput {
  width: 85%;
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  border-bottom: 1px solid #445366;
  font-size: 1em;
  font-weight: 300;
  padding-bottom: 10px;
  margin-top: 10px;
}

.formFieldInput:-ms-input-placeholder {
  color: #616e7f;
}

.formFieldInput::placeholder {
  color: #616e7f;
}

.formFieldButton {
  background-color: var(--white);
  color: var(--black);
  flex: 1 1;
  font-family: var(--font-family-circularstd-bold);
  font-size: var(--font-size-m);
  font-weight: 700;
  height: 27px;
  letter-spacing: 0;
  line-height: 30px;
  margin-bottom: 10.5px;
  margin-left: 47px;
  margin-right: 44px;
  text-align: center;
  white-space: nowrap;
}

.formFieldLink {
  color: white;
